// @mui
import { useEffect, useState } from 'react';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import { useSettingsContext } from 'src/components/settings';
import { decryptToken } from 'src/utils/common';
import axios, { endpoints } from 'src/utils/axios';
import { constants } from 'src/utils/constant';
import { useSnackbar } from 'src/components/snackbar';
import DataTable from 'react-data-table-component';
import SearchIcon from '@mui/icons-material/Search';
import TextField from '@mui/material/TextField';
import { Backdrop, Button, Chip, CircularProgress, InputAdornment } from '@mui/material';

const decryptedToken = localStorage.getItem(constants.keyUserToken);
const accessToken = decryptToken(decryptedToken);
const config = {
    headers: {
        Authorization: `Bearer ${accessToken}`
    }
};
// ----------------------------------------------------------------------

export default function CustomerView() {
    const settings = useSettingsContext();
    const { enqueueSnackbar } = useSnackbar();
    const [isLoading, setIsLoading] = useState(false);
    const [employees, setEmployees] = useState([]);
    const [attendanceLoader, setAttendanceLoader] = useState(false);
    const handleClose = () => setAttendanceLoader(false)
    const handleOpen = () => setAttendanceLoader(true)

    const [searchQuery, setSearchQuery] = useState('');



    const fetchData = async () => {

        try {
            const response = await axios.post(endpoints.employee.list, {}, config);
            if (response) {
                setIsLoading(false);
                setEmployees(response.data.data);
            }
        } catch (err) {
            enqueueSnackbar('Failed to fetch employees!', { variant: 'error' });
            setIsLoading(false);
        }
    };

    useEffect(() => {
        setIsLoading(true);
        fetchData();

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);


    const currentDate = new Date();
    const daysInMonth = new Date(currentDate.getFullYear(), currentDate.getMonth() + 1, 0).getDate();

    const handleAttendance = async (employeeId, date, currentStatus) => {
        setAttendanceLoader(true);
        // Set the default status to 'present' if it's undefined
        const defaultStatus = currentStatus === undefined ? 'present' : currentStatus;

        // Update the employee's attendance status for the given date
        const updatedEmployees = employees.map(employee => {
            if (employee._id === employeeId) {
                const updatedAttendance = {
                    ...employee.attendance,
                    [date]: defaultStatus === 'present' ? 'absent' : 'present'
                };
                // Return a new object with updated attendance
                return {
                    ...employee,
                    attendance: updatedAttendance
                };
            }
            // For other employees, return the original object
            return employee;
        });

        // Find out the the updated employee and send the updated employee to API
        const updatedEmployee = updatedEmployees.find(employee => employee._id === employeeId);
        console.log("🚀 ~ handleAttendance ~ updatedEmployee:", updatedEmployee.attendance)

        try {
            const response = await axios.post(endpoints.employee.markAttendance, { employeeId: updatedEmployee._id, attendance: updatedEmployee.attendance }, config);
            if (response) {
                enqueueSnackbar('Attendance updated!', { variant: 'success' });
                setEmployees(updatedEmployees);
                setAttendanceLoader(false);
            }
        } catch (error) {
            setAttendanceLoader(false);
            enqueueSnackbar('Failed to update attendance!', { variant: 'error' });
        };
    };

    const columns = [
        {
            name: 'Employee Names',
            selector: row => row.name,
            sortable: 'true',
            width: '150px'
        },
        ...Array.from({ length: daysInMonth }, (_, index) => {
            const date = new Date(currentDate.getFullYear(), currentDate.getMonth(), index + 1);
            // const dateString = date.toISOString().split('T')[0]; // Format date as YYYY-MM-DD
            const formattedDate = date.toLocaleDateString('en-US', { month: 'long', day: 'numeric', year: 'numeric' });
            return {
                name: formattedDate,
                center: 'true',
                cell: row => (
                    <Chip
                        label={row.attendance && row.attendance[formattedDate] === 'absent' ? 'A' : 'P'}
                        color={row.attendance && row.attendance[formattedDate] === 'absent' ? 'error' : 'success'}
                        onClick={() => handleAttendance(row._id, formattedDate, row.attendance && row.attendance[formattedDate])}
                    />
                )
            };
        })
    ];

    const filteredCustomers = employees.filter((employee) =>
        employee?.name?.toLowerCase().includes(searchQuery?.toLowerCase())
    );

    return (
        <Container maxWidth={settings.themeStretch ? false : 'xl'}>
            <Backdrop
                sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
                open={attendanceLoader}
                onClick={handleClose}
            >
                <CircularProgress color="inherit" />
            </Backdrop>

            <Typography variant="h4">Employee Attendance</Typography>

            <TextField
                sx={{ mt: 2, mb: 2 }}
                size='small'
                type="text"
                placeholder="Search Employee"
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                InputProps={{
                    startAdornment: (
                        <InputAdornment position="start">
                            <SearchIcon />
                        </InputAdornment>
                    ),
                }}
            />

            {/* Make a Datatable and list columns */}
            <DataTable
                columns={columns}
                data={filteredCustomers}
                progressPending={isLoading}
                pagination
            />

        </Container>
    );
}
